import React from 'react';

export type TPrecioBitcoin = {
  market_cap: number;
  percent_change_1h: number;
  percent_change_7h: number;
  percent_change_24h: number;
  price: number;
  volume_24: number;
};

export interface IPrecioProps {
  precioBitcoin: TPrecioBitcoin;
}
const Precio: React.FunctionComponent<IPrecioProps> = ({ precioBitcoin }) => {
  return <div>{precioBitcoin.price}</div>;
};

export default Precio;
